<template>
  <div class="line">
    <div style="width:100%;height:400px" id="chartLine"></div>
  </div>
</template>
<script>
import echarts from 'echarts';
export default {
  props: {
    data: {
      type: Array,
    },
  },
  data() {
    return {};
  },
  mounted() {
    this.drawChart();
  },
  methods: {
    drawChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChat = echarts.init(document.getElementById('chartLine'));
      // 监听屏幕变化自动缩放图表
      // 绘制图表
      myChat.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        legend: {
          data: ['利润', '支出', '收入'],
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: [
          {
            type: 'value',
          },
        ],
        yAxis: [
          {
            type: 'category',
            axisTick: {
              show: false,
            },
            data: [
              '心率',
              '年龄',
              '分诊时刻(时)',
              '休克指数',
              '性别',
              '初始分级',
              '脉压差',
              '意识状态',
              '收缩压',
              '舒张压',
              '指氧饱和度',
              '病人来源',
            ],
          },
        ],
        series: [
          {
            name: '',
            type: 'bar',
            label: {
              show: true,
              position: 'inside',
            },
            emphasis: {
              focus: 'series',
            },
            // data: [-200, -170, -240, 244, -200, 220, 210],
            data: this.data,
            itemStyle: {
              normal: {
                color: function(params) {
                  console.log(params.data);
                  return params.data > 0 ? '#FF0066' : '#1E90FF';
                },
              },
            },
          },
        ],
      });
      window.addEventListener('resize', function() {
        myChat.resize();
      });
    },
  },
  watch: {
    //监听数组变化
    data: {
      handler(newval, oldVal) {
        console.log('info.name: ' + newval, oldVal);
        this.drawChart();
      },
      deep: true,
    },
  },
};
</script>

<style scoped>
.line {
  width: 100%;
  height: auto;
}
</style>
