let SystolicPressure = (rule, value, callback) => {
  if (!value) {
    callback(new Error("不能为空"));
  }
  if (!num.test(value)) {
    callback(new Error("只能为数字"));
  }
  if (value > 300 || value < 0) {
    callback(new Error("超过阈值 阈值范围0-300"));
  }
  callback();
};
let Diastolic = (rule, value, callback) => {
  if (!value) {
    callback(new Error("不能为空"));
  }
  if (!num.test(value)) {
    callback(new Error("只能为数字"));
  }
  if (value > 200 || value < 0) {
    callback(new Error("超过阈值 阈值范围0-200"));
  }
  callback();
};
