  if (!value) {
    callback(new Error("不能为空"));
  }
  if (!num.test(value)) {
    callback(new Error("只能为数字"));
  }
  if (value > 300 || value < 0) {
    callback(new Error("超过阈值 阈值范围0-300"));
  }
  callback();
};
let Refers = (rule, value, callback) => {
  if (!value) {
    callback(new Error("不能为空"));
  }
  if (!num.test(value)) {
    callback(new Error("只能为数字"));
  }
  if (value > 300 || value < 0) {
    callback(new Error("超过阈值 阈值范围0-300"));
  }
  callback();
};
let temperature = (rule, value, callback) => {
  if (!value) {
    callback(new Error("不能为空"));
  }
  if (!num5.test(value)) {
    //可以输入小数点
    callback(new Error("只能为数字"));
  }
  if (value != "") {
    if (value < 34 || value > 44) {
      callback(new Error("超过阈值 阈值范围34-44"));
    }
  }
  callback();
};
