    },
    //是否更改
    whetheronChange(e) {
      let value = e.target.value;
      this.value = value;
      if (value == "否") {
        this.entirety.aftergrading = "";
        this.aftergrading = "无"; //
        this.entirety.GradingCause = "无"; //更改分级原因
        this.entirety.aftergrading = ""; //更改后的分级
        this.changeGrading = "无";
        this.entirety.ChangeGrading = "无";
      } else {
        this.changeGrading = "ETS triage level-IV";
        this.entirety.ChangeGrading = "IV级(不紧急)";
        this.entirety.GradingCause = "";
      }
    },
    conditionChange(val, option) {
      this.entirety.ChangeGrading = option.componentOptions.children[0].text;
    },
    onChange() {},
    //更改分级原因
    scalechange() {
      this.entirety.aftergrading = this.entirety.ChangeGrading;
      this.aftergrading = this.changeGrading; //更改后的分级
      if (this.aftergrading == "ETS triage level-III") {
        this.$refs.later.style.background = "orange";
      } else if (this.aftergrading == "ETS triage level-IV") {
        this.$refs.later.style.background = "#30e310";
      } else {
        this.$refs.later.style.background = "red";
      }
    },
    //意识状态
    mentalitytab(i, item) {
      this.entirety.MentalityState = item.name;
      this.num = item.name;
      // this.entirety.MentalityState == '清醒'
      //   ? (this.stateConsciousness = 'conscious')
      //   : (this.stateConsciousness = 'altered mental status');
      this.stateConsciousness = item.name;
    },
    //病人来源
    sourceTab(i, item) {
      this.entirety.SourcePatient = item.name;
      this.arrivalmode = item.id;
    },
    //来源说明
    explainTab(i, item) {
      this.entirety.Sources = item.name;
      this.explainNum = item.name;
    },

    //来诊主诉
    ActionChange() {},
    //成批就诊
    doctorTab(item) {
      this.doctorNum = item.id;
      this.entirety.Doctor = item.id;
    },
    //复合伤
    InjuryTab(item) {
      this.InjuryNum = item.id;
      this.entirety.CombinedInjury = item.id;
    },
    finishTab(i) {
      if (i == 0) {
        if (this.entirety.Age == "" || this.entirety.Patientnum == "") {
          this.$message.error("ID号和年龄不能为空");
          return;
        }
        let patientMainSuit = {};
        let verify = [];
        let state = true;
        this.$refs.entirety.forEach((item) => {
          item.validate((valid) => {
            verify.push(valid);
          });
        });
        state = verify.every((item) => item == true);
        if (state) {
          if (this.value == "是") {
            if (this.aftergrading == "无" || this.entirety.GradingCause == "") {
              this.$message.info("请选择更改分级 请填写分级原因");
              return;
            }
          }
          if (
            Number(this.entirety.SystolicPressure) <
            Number(this.entirety.Diastolic)
          ) {
            this.$message.info("收缩压必须大于舒张压");
            return;
          }
          this.entirety.whether = this.value;
          if (this.value == "否") {
            this.entirety.aftergrading = "无";
            this.entirety.ChangeGrading = "无";
          }
          for (const i in this.complaint) {
            if (this.complaint[i].arr.length > 0) {
              patientMainSuit[this.complaint[i].key] = this.complaint[i].arr;
            }
          }
          let obj = {};
          let isFixed = "";
          let isSeeDoctorWithBatch = "";
          let CombinedInjurys = "";
          this.value == "是" ? (isFixed = true) : (isFixed = false);
          this.entirety.Doctor == "是"
            ? (isSeeDoctorWithBatch = true)
            : (isSeeDoctorWithBatch = false);
          this.entirety.CombinedInjury == "是"
            ? (CombinedInjurys = true)
            : (CombinedInjurys = false);
          this.entirety.actionvalue = [];
          for (const i in this.complaint) {
            patientMainSuit[this.complaint[i].key] = this.complaint[i].arr;
            if (this.complaint[i].arr.length > 0) {
              this.entirety.actionvalue.push(this.complaint[i].arr.toString());
            }
          }
          console.log(this.entirety.actionvalue);
          obj = {
            name: this.entirety.name, //姓名
            caseCode: this.entirety.Patientnum, //病案号
            age: Number(this.entirety.Age), //年龄
            sex: this.sex, // 性别
            level: this.triagelevel, //来时分级
            fixLevel: this.aftergrading, //更改后的分级
            isFixed: isFixed, //是否更改分级
            changeGrading: this.changeGrading, //更改分级
            stateofconsciousness: this.stateConsciousness, //意识状态
            sourceOfPatient: this.arrivalmode, //病人来源
            sourceDesc: this.entirety.Sources, //来源说明
            remark: this.entirety.Remark, //备注
            systolicbloodpressure: Number(this.entirety.SystolicPressure), //收缩压
            diastolicbloodpressure: Number(this.entirety.Diastolic), //舒张压
            heartRate: Number(this.entirety.HeartRate), //心率
            oxygensaturation: Number(this.entirety.Refers), //指氧饱和度
            temperature: Number(this.entirety.temperature), //体温
            bloodSugar: Number(this.entirety.BloodGlucose), //血糖
            patientMainSuit: patientMainSuit, //来诊主诉
            other: this.entirety.Else, //其他
            anamnesis: this.entirety.anamnesis.toString(), //既往病史
            isSeeDoctorWithBatch: isSeeDoctorWithBatch, //成批就诊
            total: Number(this.entirety.Headcount), //总人数
            isCompoundInjury: CombinedInjurys, //复合伤
            gradingCause:
              this.value == "否"
                ? (this.entirety.GradingCause = "无")
                : this.entirety.GradingCause, //更改分级原因
          };
          // this.$ajax.baseURL =
          //   process.env.NODE_ENV == "development"
          //     ? "/api"
          //     : "http://127.0.0.1:3001";
          this.spinning = true;
          this.$ajax
            // .post(`${this.$ajax.baseURL}/getResult`, obj)
            .post('http://127.0.0.1:3000/getResult', obj)
            .then((res) => {
              this.spinning = false;
              let whengrading = "";
              console.log(res);
              this.triagelevel = res.data.level;
              if (
                this.triagelevel == "ETS triage level-III" &&
                res.data.data != "No need to alarm.\r\n"
              ) {
                this.visible = true;
                this.$refs.grading.style.background = "orange";
                this.echartsData = res.data.data;
                console.log(this.echartsData);
              } else if (
                this.triagelevel == "ETS triage level-IV" &&
                res.data.data != "No need to alarm.\r\n"
              ) {
                this.visible = true;
                this.echartsData = res.data.data;
                this.$refs.grading.style.background = "#30e310";
              } else {
                this.entirety.result = "无图例";
                this.deposit();
                this.$refs.grading.style.background = "red";
              }
              whengrading = this.Whengrading.find(
                (value) => value.id == res.data.level
              );
              this.entirety.WhenGrading = whengrading.name;
              // console.log(this.entirety.WhenGrading);
              // console.log(obj, '/////////');
              Message({
                duration: 0,
                showClose: true,
                message: `${obj.name} 30分钟后查看提醒`,
                type: "warning",
                customClass: "widthMessage",
              });
            })
            .catch((error) => {
              this.spinning = false;
              this.$message.info("加载失败");
              console.log(error);
            });
        } else {
          console.log("失败");
          this.$message.info("保存失败");
        }
      } else {
        this.$refs.entirety.forEach((item) => {
          item.resetFields();
        });
        this.show = false;
        this.changeGrading = "";
        this.entirety.name = "";
        this.entirety.BloodGlucose = "";
        this.entirety.Patientnum = "";
        this.entirety.Age = "";
        this.entirety.Patientnum = "";
        this.entirety.Sex = "男";
        this.triagelevel = "";
        this.entirety.WhenGrading = ""; //来时分级
        this.entirety.ChangeGrading = "IV级(不紧急)";
        this.value = "否";
        this.aftergrading = "无";
        this.entirety.aftergrading = "IV级(不紧急)"; //更改后的分级
        this.stateConsciousness = ""; //意识状态
        this.entirety.GradingCause = "";
        this.entirety.MentalityState = "";
        this.entirety.SourcePatient = "";
        this.entirety.Sources = "";
        this.entirety.Remark = "";
        this.entirety.Else = "";
        this.entirety.anamnesis = [];
        this.entirety.Doctor = "";
        this.entirety.Headcount = "";
        this.entirety.medicalHistory = "";
        this.entirety.CombinedInjury = "";
        this.num = "";
        this.arrivalmode = "";
        this.explainNum = "";
        this.doctorNum = "";
        this.InjuryNum = "";
        for (const i in this.complaint) {
          this.complaint[i].arr = [];
        }
        this.$refs.grading.style.background = "#30e310";
        this.$refs.later.style.background = "#30e310";
        this.$message.info("清空成功");
      }
      this.finishNum = i;
    },
    //存入excel
    deposit() {
      // this.entirety.WhenGrading = this.entirety.aftergrading;
      let datalist = [];
      datalist.push([
        "姓名",
        "病历号",
        "年龄",
        "性别",
        "来时分级",
        "更改后的分级",
        "是否更改分级",
        "更改分级",
        "更改分级原因",
        "意识状态",
        "病人来源",
        "来源说明",
        "备注",
        "生命体征-收缩压",
        "生命体征-舒张压",
        "生命体征-心率",
        "生命体征-指氧饱和度",
        "生命体征-体温",
        "生命体征-血糖",
        "来诊主诉",
        "其他",
        "既往病史",
        "其他",
        "成批就诊",
        "总人数",
        "复合伤",
        "结果",
      ]);
      let data = [];
      for (const key in this.entirety) {
        console.log(this.entirety[key]);
        data.push(this.entirety[key].toString());
      }
      console.log(data);
      datalist.push(data);
      console.log(datalist);
      this.nowDate = moment().format("YYYY-MM-DD HH:mm:ss");
      let name = `${this.nowDate}` + "急诊数据统计信息" + ".xlsx";
      console.log("急诊数据统计信息" + `${this.nowDate}` + ".xlsx");
      downloadXlsx(datalist, name);
    },
  },
};
</script>

